import React from 'react';
import '../styles/containers/mobileNav.css';

export const MobileNav = ({ items, show }) => (
  <ul className="mobile">
    {
      items.map((item, key) => {
        return <li className="mobile__item" key={key}>{ item.text }</li>
      })
    }
  </ul>
)


